import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {HciClusterService} from "@generated/hci-cluster-service";
import {ManagedObject} from "@service/managed-object";
import {VsanTestData} from "@generated/vsan-test-data";
import {VsanHealthUtil} from "@util/vsan-health-util";
import {Logger} from "@util/logger";
import {PermissionService} from '@generated/permission-service';
import {Privileges} from '@generated/privileges';
import {HealthTests} from "../../health/model/health-tests.data";

@Component({
   templateUrl: "health-check-dialog.html",
   selector: "vsan-health-check-dialog",
   styleUrls: ['health-check-dialog.scss']
})
export class HealthCheckDialogComponent implements OnInit {

   title: string;
   alertMessages: string[] = [];
   loading: boolean;

   perspective: string;
   healthGroup: string;
   healthTest: string;

   displayGroup: boolean;
   testData: VsanTestData;
   groupTests: HealthTests[];
   hasEditClusterPermission: boolean = false;

   constructor(private hciService: HciClusterService,
         private permissionService: PermissionService) {}

   ngOnInit(): void {
      this.loading = true;

      const context: any = VsanUiUtils.getModalContext();
      this.title = context.title;
      this.perspective = context.perspective;
      this.healthGroup = context.healthGroup;
      this.healthTest = context.healthTest;
      this.displayGroup = !this.healthTest;

      Promise.all([
         this.permissionService.hasPermissions(ManagedObject.contextObject, [Privileges.EDIT_CLUSTER()]),
         this.hciService.getHealthGroupData(ManagedObject.contextObject, this.perspective, this.healthGroup),
      ]).then(result => {
         this.hasEditClusterPermission = result[0];
         this.handleHealthData(result[1]);
      }).catch(this.handleError);
   }

   handleHealthData = (result: VsanTestData) => {
      if (!result) {
         this.handleError(null);
         return;
      }

      if (this.displayGroup) {
         this.groupTests = result.subtests.map((subtest: VsanTestData) => {
            return new HealthTests(subtest, true);
         });
      } else {
         this.testData = result.subtests.find((test) => test.testName === this.healthTest);
      }

      this.loading = false;
   };

   private handleError = (error: string) => {
      if (error) {
         Logger.error(error);
      }

      this.alertMessages = [VsanUiUtils.getString("vsan.common.error.dataExtraction")];
      this.loading = false;
   };

   getIconShape = (test: VsanTestData): string => {
      return VsanHealthUtil.getVsanHealthStatusIconShape(test.status);
   };

   getIconClass = (test: VsanTestData): string => {
      return VsanHealthUtil.getVsanHealthStatusIconClass(test.status);
   };
}