import {Component, OnDestroy, OnInit} from "@angular/core";
import {AlertType} from "@component/validation/alert-type";
import {BasicClusterConfigData} from "@generated/basic-cluster-config-data";
import {ConfigureWizardData} from "@generated/configure-wizard-data";
import {HciBasicCardData} from "@generated/hci-basic-card-data";
import {HciClusterService} from "@generated/hci-cluster-service";
import {HciConfigureClusterCardData} from "@generated/hci-configure-cluster-card-data";
import {HciQuickstartService} from "@generated/hci-quickstart-service";
import {HciQuickstartValidationService} from "@generated/hci-quickstart-validation-service";
import {HciValidationCard} from "@generated/hci-validation-card";
import {HciValidationData} from "@generated/hci-validation-data";
import {HciWorkflowState} from "@generated/hci-workflow-state";
import {QuickstartViewData} from "@generated/quickstart-view-data";
import {VsanHealthCheck} from "@generated/vsan-health-check";
import {VsanHealthStatus} from "@generated/vsan-health-status";
import {HciConfigureWizardContext} from "@navigation/context/hci-configure-wizard-context";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {RefreshHandler} from "@service/global-refresh.service";
import {ManagedObject} from "@service/managed-object";
import {ScreenReaderAnnouncer} from "@service/screen-reader-announcer.service";
import {TaskMonitorService} from "@service/task-monitor-service";
import {DomUtil} from "@util/dom-util";
import {Icon} from "@util/icon";
import {Logger} from "@util/logger";
import {ModalBuilder} from "@util/modal-builder";
import {VsanUiUtils} from "@util/vsan-util";
import {Subject} from "rxjs";
import {auditTime} from "rxjs/operators";
import {VsanRoutes} from "../../../vsan-routes";
import {GettingStartedUtils} from "./getting-started.utils";

@Component({
   templateUrl: "getting-started-view.html",
   styleUrls: ["getting-started-view.scss"],
   selector: "vsan-hci-getting-started",
   host: {class: "getting-started-view"}, // Class required by the H5C. DO NOT REMOVE!
})
export class GettingStartedViewComponent implements OnInit, OnDestroy {

   private static readonly MESSAGE: string = "message";
   private static readonly REFRESH_EVENT_NAME: string = "refreshQuickstart";

   private static readonly DISMISS_DIALOG_WIDTH: number = 750;
   private static readonly DISMISS_DIALOG_HEIGHT: number = 250;
   private static readonly CONFIGURE_CONFIRM_DIALOG_WIDTH: number = 576;
   private static readonly CONFIGURE_CONFIRM_DIALOG_HEIGHT: number = 250;
   private static readonly CONFIGURE_WARNING_DIALOG_WIDTH: number = 700;
   private static readonly CONFIGURE_WARNING_DIALOG_HEIGHT: number = 200;
   private static readonly CONFIGURE_WARNING_NETWORK_DIALOG_HEIGHT: number = 270;
   private static readonly REFRESH_DEBOUNCE_MS: number = 2000;
   private static readonly CHECK_OPERATION_TIMEOUT_MS: number = 5000;

   GettingStartedUtils: typeof GettingStartedUtils = GettingStartedUtils;
   AlertType: typeof AlertType = AlertType;
   Icon: typeof Icon = Icon;

   @LoadingIndicator()
   loading: boolean;

   @ErrorMessage()
   alertMessage: string;

   title: string = VsanUiUtils.getString("vsan.cluster.hci.gettingStartedPage.title");
   text: string;
   clusterBasicsCard: HciBasicCardData;
   addHostsCard: HciValidationCard;
   configureClusterCard: HciConfigureClusterCardData;
   enabledServices: string[];
   basicClusterData: BasicClusterConfigData;

   showCloseQuickstartButton: boolean = false;
   isVsanMaxSupportedOnHosts: boolean = false;

   @ScreenReaderAnnouncer()
   srOnlyMessage: string;

   private refreshStream: Subject<void> = new Subject<void>();
   private configOperationTimer: number;
   private originalConfigureCardEnabled: boolean;
   private isExtend: boolean;

   constructor(private hciQuickstartService: HciQuickstartService,
         private hciQuickstartValidationService: HciQuickstartValidationService,
         private hciConfigureService: HciClusterService,
         private taskService: TaskMonitorService,
         private navigationService: NavigationService) {
   }

   ngOnInit(): void {
      this.fetch();
      this.registerRefreshEvents();
   }

   @RefreshHandler
   @Loader()
   async fetch(): Promise<void> {
      this.handleCardsData(await this.hciQuickstartService.getGettingStartedData(ManagedObject.contextObject));
      this.validateCards();
      this.updateConfigOperationInterval();
      this.srOnlyMessage = VsanUiUtils.getString("vsan.screenReader.message.page.loaded",
            VsanUiUtils.getString("vsan.cluster.hci.gettingStartedPage.title"));
   }

   private handleCardsData(viewData: QuickstartViewData): void {
      this.basicClusterData = viewData.basicClusterData;
      this.title = viewData.header;
      this.text = viewData.text;
      this.enabledServices = GettingStartedUtils.getEnabledServices(this.basicClusterData);
      this.showCloseQuickstartButton = viewData.showCloseQuickstartButton;
      this.clusterBasicsCard = viewData.clusterBasicsCard;
      this.addHostsCard = viewData.addHostsCard;
      this.configureClusterCard = viewData.configureClusterCard;
      // Store this to be able to restore it after host are validated.
      this.originalConfigureCardEnabled = this.configureClusterCard.enabled;
      this.isExtend = viewData.extendCard;
   }

   private updateConfigOperationInterval(): void {
      if (this.configOperationTimer) {
         clearInterval(this.configOperationTimer);
      } else if (this.configureClusterCard.isConfigureOperationInProgress) {
         this.configOperationTimer = this.checkConfigOperationInProgress();
      }
   }

   private async validateCards(): Promise<void> {
      if (this.addHostsCard.validateEnabled) {
         await this.validateHosts();
      } else if (this.configureClusterCard.validateEnabled) {
         await this.validateCluster();
      }

      /* After the health checks are loaded and displayed in the second/third card. all the cards grow as
       we keep them all with equal size. Unfortunately in some Chrome versions the cards are not rendered correctly
       after their size is changed. This causes the buttons in the footer to not be clickable. For more info please
       check bug https://bz3-prod-wwwd.eng.vmware.com/show_bug.cgi?id=2392360
       The solution is to force the browser to redraw the problematic DOM elements.
       */
      // TO DO: Check if we still need this? The above mentioned PR has nothing to do with the comment.
      DomUtil.redraw(".card");
   }

   async validateHosts(): Promise<void> {
      this.addHostsCard.validateInProgress = true;
      // Disable the configure action, while hosts are being validated
      this.configureClusterCard.enabled = false;
      this.isVsanMaxSupportedOnHosts = false;
      try {
         [this.addHostsCard.validationData, this.isVsanMaxSupportedOnHosts] = await Promise.all([
            this.hciQuickstartValidationService.validateNotConfiguredHosts(ManagedObject.contextObject),
            this.hciQuickstartValidationService.validateVsanMaxHostsSupport(ManagedObject.contextObject),
         ]);

         // If there are no errors in hosts validation, configure cluster card's enabled state should be reverted
         if (HciValidationData.Status.ERROR !== this.addHostsCard.validationData.status) {
            this.configureClusterCard.enabled = this.originalConfigureCardEnabled;
         }

      } catch (e) {
         Logger.error(e);
         this.alertMessage = e;
      } finally {
         this.addHostsCard.validateInProgress = false;
      }
   }

   async validateCluster(): Promise<void> {
      this.configureClusterCard.validateInProgress = true;
      try {
         this.configureClusterCard.validationData = await this.hciQuickstartValidationService
               .validateCluster(ManagedObject.contextObject);
      } catch (e) {
         Logger.error(e);
      } finally {
         // TODO: Anyone that understands this, please add comment why this must happen here???
         if (!this.configureClusterCard.isConfigureOperationInProgress && this.configOperationTimer) {
            clearInterval(this.configOperationTimer);
         }
         this.configureClusterCard.validateInProgress = false;
      }
   }

   showVsanMaxPrechecks(): boolean {
      return this.basicClusterData.isVsanMaxEnabled
            && this.basicClusterData.hciWorkflowState !== HciWorkflowState.INVALID
            && this.basicClusterData.hciWorkflowState !== HciWorkflowState.DONE
            && !this.addHostsCard.validateInProgress;
   }

   promptUserToEnableVsanMax(): boolean {
      return this.basicClusterData?.vsanEnabled
            && this.basicClusterData.notConfiguredHosts > 0
            && this.basicClusterData.hciWorkflowState === HciWorkflowState.IN_PROGRESS
            && !this.basicClusterData?.isVsanMaxEnabled
            && this.isVsanMaxSupportedOnHosts;
   }

   getVsanMaxHealthChecks(card: HciValidationCard): VsanHealthCheck[] {
      return card?.validationData?.vsanHealthChecks?.filter(check => check.isHardwareCheck);
   }

   getGenericHealthChecks(card: HciValidationCard): VsanHealthCheck[] {
      return card?.validationData?.vsanHealthChecks?.filter(check => !check.isHardwareCheck);
   }

   /**
    * Checks if there are any vsan max related prechecks in error state in order to display
    * warning in the Configure cluster card
    */
   isVsanMaxConfigurationInvalid(): boolean {
      return this.getVsanMaxHealthChecks(this.addHostsCard)?.filter(
            check => check.status === VsanHealthStatus.red).length > 0;
   }

   getVsanMaxConfigurationWarningMessage(): string {
      return this.isExtend
            ? VsanUiUtils.getString("vsan.hci.gettingStarted.configureServicesCard.vsanMaxWarning.extend")
            : VsanUiUtils.getString("vsan.hci.gettingStarted.configureServicesCard.vsanMaxWarning.initial");
   }

   getValidateResultIconShape(card: HciValidationCard): string {
      if (card.validationData?.status === HciValidationData.Status.ERROR) {
         return Icon.CLR_ERROR_STANDARD_ICON_SHAPE;
      } else if (card.validationData?.status === HciValidationData.Status.PASSED) {
         return Icon.CLR_SUCCESS_STANDARD_ICON_SHAPE;
      } else {
         return null;
      }
   }

   getValidateResultIconClass(card: HciValidationCard): string {
      if (card.validationData?.status === HciValidationData.Status.ERROR) {
         return Icon.CLR_ERROR_ICON_CLASS;
      } else if (card.validationData?.status === HciValidationData.Status.PASSED) {
         return Icon.CLR_SUCCESS_ICON_CLASS;
      } else {
         return null;
      }
   }

   invokeEditCluster(): void {
      const actionEval: any = {
         action: {
            label: VsanUiUtils.getString("vsan.cluster.hci.clusterBasics.edit.action"),
         },
      };
      this.navigationService.invokePlatformAction(PlatformExtension.CLUSTER_EDIT, [ManagedObject.contextObjectUid],
            null, actionEval);
   }

   invokeAddHosts(): void {
      this.navigationService.invokePlatformAction(PlatformExtension.ADD_HOSTS, [ManagedObject.contextObjectUid]);
   }

   async invokeConfigureWizard(): Promise<void> {
      try {
         this.configureClusterCard.enabled = false;
         const result: ConfigureWizardData = await this.hciConfigureService
               .getConfigureWizardData(ManagedObject.contextObject);
         this.handleConfigureWizardData(result);
      } catch (e) {
         this.alertMessage = e;
         Logger.error(e);
      } finally {
         this.configureClusterCard.enabled = true;
      }
   }

   private handleConfigureWizardData(result: ConfigureWizardData): void {
      if (result.openYesNoDialog) {
         new ModalBuilder("hciCluster/confirmationDialog")
               .setSize(
                     GettingStartedViewComponent.CONFIGURE_CONFIRM_DIALOG_WIDTH,
                     GettingStartedViewComponent.CONFIGURE_CONFIRM_DIALOG_HEIGHT)
               .open({title: result.dialogText})
               .then(() => {
                  this.refresh();
               });
      } else if (result.openWarningDialog) {
         new ModalBuilder("hciCluster/warningDialog")
               .setSize(
                     GettingStartedViewComponent.CONFIGURE_WARNING_DIALOG_WIDTH,
                     result.warningDialogContent && result.warningDialogContent.length
                           ? GettingStartedViewComponent.CONFIGURE_WARNING_NETWORK_DIALOG_HEIGHT
                           : GettingStartedViewComponent.CONFIGURE_WARNING_DIALOG_HEIGHT)
               .open({
                  title: result.dialogText,
                  content: result.warningDialogContent,
               })
               .then(() => {
                  this.refresh();
               });
      } else {
         const configureWizardContext: HciConfigureWizardContext = {
            configureWizardData: result,
            isVsanMaxSupportedOnHosts: this.isVsanMaxSupportedOnHosts,
         };
         new ModalBuilder(VsanRoutes.HCI_CONFIGURE_WIZARD)
               .setDimensions(ModalBuilder.WIZARD_XL)
               .open(configureWizardContext)
               .then((task: ManagedObject) => {
                  // trigger refresh in order to disable the card until the task is done
                  this.fetch();

                  this.taskService.getAwaitResult(task).then(() => {
                     this.refresh();
                  });
               }); // catch is impossible
      }
   }

   // Repeatedly check if the operation has failed in order to avoid indefinite card loading.
   // If the operation has failed, refresh is be triggered.
   private checkConfigOperationInProgress(): any {
      return setInterval(() => {
         this.hciQuickstartService.isConfigureOperationInProgress(ManagedObject.contextObject)
               .then((result: boolean) => {
                  // when the operation is no longer in progress refresh is triggered
                  if (!result) {
                     this.refresh();
                     clearInterval(this.configOperationTimer);
                  }
               })
               .catch(() => {
                  clearInterval(this.configOperationTimer);
               });

      }, GettingStartedViewComponent.CHECK_OPERATION_TIMEOUT_MS);
   }

   closeQuickstart(): void {
      new ModalBuilder("hciCluster/dismissQuickstart")
            .setSize(
                  GettingStartedViewComponent.DISMISS_DIALOG_WIDTH,
                  GettingStartedViewComponent.DISMISS_DIALOG_HEIGHT)
            .open()
            .then(() => {
               this.showCloseQuickstartButton = true;
               this.refresh();
            });
   }

   private registerRefreshEvents = () => {
      window.addEventListener(
            GettingStartedViewComponent.MESSAGE,
            (event: any) => {
               if (event.data === GettingStartedViewComponent.REFRESH_EVENT_NAME) {
                  this.refresh();
               }
            },
            false);

      this.refreshStream.pipe(auditTime(GettingStartedViewComponent.REFRESH_DEBOUNCE_MS))
            .subscribe(() => {
               this.fetch();
            });
   };

   refresh(): void {
      // The data fetch will not happen more than once every two seconds.
      // It will be triggered at the end of the interval.
      // This is done in order to prevent firing multiple data fetch requests
      // at the same time due to live and self refresh.
      this.refreshStream.next();
   }

   ngOnDestroy(): void {
      (window as any).removeEventListener(GettingStartedViewComponent.MESSAGE);
      if (this.configOperationTimer) {
         clearInterval(this.configOperationTimer);
      }
   }
}
