import {Component, OnInit} from "@angular/core";
import {HciClusterService} from "@generated/hci-cluster-service";
import {ClusterComputeResource} from "@service/client/dataservice/data-service-property";
import {DataServiceClient} from "@service/client/dataservice/data-service.service";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   template: `
      <vsan-prompt
            [title]="title"
            [confirmText]="'vsan.common.continue'"
            [cancelText]="'vsan.common.cancel'"
            [blockContent]="busy"
            [blockButton]="busy"
            (onConfirm)="proceed()"
            (onCancel)="close()">
         <span>
            {{"vsan.cluster.hci.configureHciCluster.dialog.text" | localized}}
         </span>
         <span *ngIf="showLockdownMode">
            {{"vsan.cluster.hci.configureHciCluster.dialog.lockdownModeText" | localized:lockdownModeString}}
         </span>
      </vsan-prompt>
   `,
   selector: "vsan-configure-confirmation"
})
export class ConfigureConfirmationDialogComponent implements OnInit {

   busy: boolean;
   title: boolean;
   showLockdownMode: boolean = false;
   lockdownModeString: string;

   constructor(private hciClusterService: HciClusterService, private dataService: DataServiceClient) {
   }

   async ngOnInit(): Promise<void> {
      this.busy = true;
      this.title = VsanUiUtils.getModalContext().title;
      const lockdownMode: string = await this.dataService
            .getProperty(ClusterComputeResource.hciConfig.hostConfigProfile.lockdownMode, ManagedObject.contextObject);
      this.showLockdownMode = !("lockdownDisabled" === lockdownMode);
      if (this.showLockdownMode) {
         this.lockdownModeString = ("lockdownNormal" === lockdownMode)
               ? VsanUiUtils.getString("vsan.cluster.configure.wizard.advancedOptions.host.options.lockdown.mode.normal")
               : VsanUiUtils.getString("vsan.cluster.configure.wizard.advancedOptions.host.options.lockdown.mode.strict");
      }
      this.busy = false;
   }

   proceed = (): void => {
      this.hciClusterService.simpleClusterExtend(ManagedObject.contextObject)
            .then(() => {
               this.close()
            });
   };

   close = (): void => {
      VsanUiUtils.closeModalDialog(true);
   };
}
