import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   template:`
      <vsan-info-prompt [title]="title"
                        [isCallbackTriggerEnabled]="true"
                        confirmText="vsan.common.ok">
         <vsan-validation *ngIf="incompatibleNetworkConfig"
                          [alertType]="'danger'"
                          [allowClose]="false"
                          [alert]="'vsan.cluster.hci.configureHciCluster.warningDialog.errorMessage' | localized">
         </vsan-validation>
         <p *ngFor="let message of content">{{message}}</p>
      </vsan-info-prompt>
   `,
   selector: "vsan-configure-warning"
})
export class ConfigureWarningDialogComponent implements OnInit {

   title: boolean;
   content: string[];
   incompatibleNetworkConfig: boolean;

   ngOnInit(): void {
      this.title = VsanUiUtils.getModalContext().title;
      if (VsanUiUtils.getModalContext().content) {
         this.content = VsanUiUtils.getModalContext().content;
         this.incompatibleNetworkConfig = true;
      } else {
         this.content = [VsanUiUtils.getString("vsan.cluster.hci.configureHciCluster.warningDialog.text")];
      }
   }
}