import {Component} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {HciClusterService} from "@generated/hci-cluster-service";

@Component({
   template:`
      <vsan-prompt
            [title]="'vsan.cluster.hci.gettingStartedPage.closeQuickstart.title' | localized"
            [confirmText]="'vsan.common.continue'"
            [cancelText]="'vsan.common.cancel'"
            [confirmStyle]="'btn-danger'"
            [blockContent]="busy"
            [blockButton]="busy"
            (onConfirm)="continue()"
            (onCancel)="close()">
         {{"vsan.cluster.hci.gettingStartedPage.closeQuickstart.description" | localized}}
      </vsan-prompt>
   `,
   selector: "vsan-dismiss-quickstart"
})
export class DismissQuickstartDialogComponent {

   busy: boolean;

   constructor(private hciClusterService: HciClusterService) {}

   continue = (): void => {
      this.busy = true;
      this.hciClusterService.abandonHciWorkflowCluster(ManagedObject.contextObject)
            .then(() => {
               this.busy = false;
               this.close(true)
            });
   };

   close = (isDialogClosed: boolean = undefined): void => {
      VsanUiUtils.closeModalDialog(isDialogClosed);
   };
}
