import {FormArray, FormControl, FormGroup} from "@angular/forms";

export class ReactiveFormUtil {

   private static readonly FORM_CONTROLS_DELIMITER: string = ".";

   /*
    * Trigger validation on all controls in a form group and mark them as touched to display errors
    */
   static validateAllFormFields = (formGroup: FormGroup): void => {
      formGroup.markAsTouched();
      Object.keys(formGroup.controls).forEach(field => {
         const control = formGroup.get(field);
         control.markAsTouched({ onlySelf: true });
         if (control instanceof FormGroup) {
            ReactiveFormUtil.validateAllFormFields(control);
         } else if (control instanceof FormArray) {
            control.controls.forEach(
                  (childControl: FormGroup) => ReactiveFormUtil.validateAllFormFields(childControl));
         } else if (control instanceof FormControl) {
            ReactiveFormUtil.validateControls([control]);
         }
      });
   };

   static validateControls = (controls: FormControl[]): void => {
      controls.forEach((control) => {
         control.markAsTouched({onlySelf: true});
         control.updateValueAndValidity({
            onlySelf: false,
            emitEvent: true
         });
      });
   };

   /**
    * Trigger updateValueAndValidity() to call ValidatorFn and update validity of all controls in a form group.
    */
   static updateFormControlsValueAndValidity = (formGroup: FormGroup) => {
      Object.keys(formGroup.controls).forEach(control => {
         if (formGroup.get(control) instanceof FormControl) {
            formGroup.get(control).updateValueAndValidity();
         }
      })
   };

   static getFormControlPath = (...parts: string[]): string => {
      return parts.join(ReactiveFormUtil.FORM_CONTROLS_DELIMITER);
   };
}
