/**
 * Util methods for manipulating DOM
 */
export class DomUtil {
   static onClick(element: HTMLElement, callback: () => any): void {
      element.addEventListener("click", () => callback());
   }

   static onEnterKeyUp(element: HTMLElement, callback: () => any): void {
      element.addEventListener("keyup", (event) => {
         // Handle "Enter" key on the keyboard
         if (event.keyCode === 13) {
            callback();
         }
      });
   }

   static onMouseOver(element: HTMLElement, callback: () => any): void {
      element.addEventListener("mouseover", () => callback());
   }

   static onMouseOut(element: HTMLElement, callback: () => any): void {
      element.addEventListener("mouseout", () => callback());
   }

   /**
    * Causes the DOM elements matching the given selector to be redrawn by the browser's layout enging.
    * This is useful when the browser fails to render some elements correctly and by redrawing them, this is fixed.
    */
   static redraw(selector: string): void {
      setTimeout(() => {
         $(selector).hide().show(0);
      }, 100);
   }

   static domElementExists(elementName: string): boolean {
      return !!document.getElementById(elementName);
   }
}