import {VsanUiUtils} from "@util/vsan-util";
import {BasicClusterConfigData} from "@generated/basic-cluster-config-data";
import {HciConfigureClusterCardData} from "@generated/hci-configure-cluster-card-data";
import {HciWorkflowState} from "@generated/hci-workflow-state";

export class GettingStartedUtils {

   static getEnabledServices(basicClusterConfigData: BasicClusterConfigData): string[] {
      const result: string[] = [];

      if (basicClusterConfigData.drsEnabled) {
         result.push(VsanUiUtils.getString("vsan.hci.gettingStarted.basicConfigCard.services.drs"));
      }

      if (basicClusterConfigData.haEnabled) {
         result.push(VsanUiUtils.getString("vsan.hci.gettingStarted.basicConfigCard.services.ha"));
      }

      if (basicClusterConfigData.vsanEnabled) {
         result.push(VsanUiUtils.getString(basicClusterConfigData.isVsanMaxEnabled ?
               "vsan.hci.gettingStarted.basicConfigCard.services.vsanMax" :
               "vsan.hci.gettingStarted.basicConfigCard.services.vsan"));
      }

      if (basicClusterConfigData.isComputeOnlyCluster) {
         result.push(VsanUiUtils.getString("vsan.hci.gettingStarted.basicConfigCard.services.vsanComputeOnly"));
      }

      return result;
   }

   static getClusterBasicsHeader(enabledServices: string[]): string {
      return  VsanUiUtils.getString(enabledServices.length > 0
            ? "vsan.hci.gettingStarted.basicConfigCard.contentHeader.selectedServices"
            : "vsan.hci.gettingStarted.basicConfigCard.contentHeader.noServices");
   }

   static getConfigureClusterHeader(isExtend: boolean) : string {
      return  VsanUiUtils.getString(isExtend
            ? "vsan.hci.gettingStarted.configureServicesCard.titleInExtend"
            : "vsan.hci.gettingStarted.configureServicesCard.title");
   }

   static getHostsLabel(basicClusterConfigData: BasicClusterConfigData): string {
      if (basicClusterConfigData.notConfiguredHosts !== 0) {
         if (basicClusterConfigData.notConfiguredHosts === basicClusterConfigData.totalHosts) {
            return VsanUiUtils.getString(
                  "vsan.hci.gettingStarted.addHostsCard.notConfiguredHostsInTheCluster",
                  basicClusterConfigData.notConfiguredHosts);
         } else {
            return VsanUiUtils.getString(
                  "vsan.hci.gettingStarted.addHostsCard.hostsInTheClusterByType",
                  basicClusterConfigData.totalHosts,
                  basicClusterConfigData.notConfiguredHosts);
         }
      } else {
         return VsanUiUtils.getString(
               "vsan.hci.gettingStarted.addHostsCard.hostsInTheCluster",
               basicClusterConfigData.totalHosts);
      }
   }

   static getConfiguredClusterHeaderLabel(configData: BasicClusterConfigData): string {
      if (configData.hciWorkflowState === HciWorkflowState.DONE && configData.notConfiguredHosts > 0) {
         return configData.notConfiguredHosts === 1 ?
               VsanUiUtils.getString("vsan.hci.gettingStarted.configureServicesCard.notConfiguredHostText") :
               VsanUiUtils.getString("vsan.hci.gettingStarted.configureServicesCard.notConfiguredHostsText",
                     configData.notConfiguredHosts);
      }
      return null;
   }

   static getConfigureCardContentText(configureClusterCard: HciConfigureClusterCardData,
         basicClusterConfigData: BasicClusterConfigData): string {
      if (configureClusterCard.validateEnabled && configureClusterCard.validationData) {
         // When validation has passed, only the validation result will be shown in the cluster card.
         return null;
      }
      if (basicClusterConfigData.drsEnabled && basicClusterConfigData.vsanEnabled) {
         return VsanUiUtils.getString("vsan.hci.gettingStarted.configureServicesCard.contentText.vMotionVsanTraffic");
      }
      if (basicClusterConfigData.drsEnabled) {
         return VsanUiUtils.getString("vsan.hci.gettingStarted.configureServicesCard.contentText.vMotionTraffic");
      }
      if (basicClusterConfigData.vsanEnabled) {
         return VsanUiUtils.getString("vsan.hci.gettingStarted.configureServicesCard.contentText.vSanTraffic");
      }

      return VsanUiUtils.getString("vsan.hci.gettingStarted.configureServicesCard.contentText.default");
   }
}