import {NgModule} from "@angular/core";
import {ClarityModule} from "@clr/angular";
import {FormsModule} from "@angular/forms";
import {CommonModule} from "@angular/common";
import {RouterModule, Routes} from "@angular/router";
import {CommonPipeModule} from "@pipe/common-pipe.module";
import {PromptModule} from "@component/dialog/prompt/prompt.module";
import {HealthDetailsCardModule} from "../health/health-details-card.module";
import {HealthCheckDialogComponent} from "./health/health-check-dialog.component";
import {GettingStartedViewComponent} from "./gettingstarted/getting-started-view.component";
import {DismissQuickstartDialogComponent} from "./dismiss/dismiss-quickstart-dialog.component";
import {ConfigureWarningDialogComponent} from "./configure/dialog/configure-warning-dialog.component";
import {ConfigureConfirmationDialogComponent} from "./configure/dialog/configure-confirmation-dialog.component";
import {ValidationModule} from "@component/validation/validation.module";
import {CommonDirectiveModule} from "@directive/common-directive.module";
import {HealthCheckItemModule} from "@component/health/health-check-item.module";
import {HciPmanComponent} from "./gettingstarted/hci-pman.component";

const hciRoutes: Routes = [
   {
      path: "",
      component: GettingStartedViewComponent,
   },
   {
      path: "warningDialog",
      component: ConfigureWarningDialogComponent,
   },
   {
      path: "confirmationDialog",
      component: ConfigureConfirmationDialogComponent,
   },
   {
      path: "healthCheckDialog",
      component: HealthCheckDialogComponent,
   },
   {
      path: "dismissQuickstart",
      component: DismissQuickstartDialogComponent,
   },
];

@NgModule({
   imports: [
      FormsModule,
      CommonModule,
      PromptModule,
      ClarityModule,
      CommonPipeModule,
      CommonDirectiveModule,
      HealthDetailsCardModule,
      ValidationModule,
      HealthCheckItemModule,
      RouterModule.forChild(hciRoutes),
   ],
   declarations: [
      HealthCheckDialogComponent,
      GettingStartedViewComponent,
      HciPmanComponent,
      ConfigureWarningDialogComponent,
      DismissQuickstartDialogComponent,
      ConfigureConfirmationDialogComponent,
   ],
})
export class GettingStartedViewModule {
}
